import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import './styles.css';
import { questionsTakeRequest } from '../../../redux-saga/actions/questionsActions';
import { multfilmTakeRequare } from '../../../redux-saga/actions/multfilmsActions';
import { getAccountPendingSelector, getAccountSelector } from '../../../redux-saga/selectors/userSelector';
import { getMultfilmsPendingSelector, getMultfilmsSelector } from '../../../redux-saga/selectors/multfilmsSelector';
import Test from './Test';

const Progres = () => {

  const pendingAccount = useSelector(getAccountPendingSelector);
  const pendingMultfilms = useSelector(getMultfilmsPendingSelector);
  const authInfo = useSelector(getAccountSelector);
  const multfilms = useSelector(getMultfilmsSelector);
  const dispatch = useDispatch();
  const level = authInfo.involvement;

  const getInfo = async () => {
    if (authInfo.test_passed) {
      dispatch(multfilmTakeRequare());
    }
    if (authInfo.name) {
      const action = {level, topic: 'newcomers'}
      dispatch(questionsTakeRequest(action));
    }
  };

  useEffect(() => {
    getInfo();
  }, [authInfo.test_passed, authInfo.name]);

  return (
    <div className="multfilms">
      {!pendingAccount && authInfo
      && (
        (!authInfo.name && <span>Ты не авторизован</span>)
        || (!authInfo.test_passed && <Test />)
        || (!pendingMultfilms && multfilms && multfilms.map((multfilm) => (
          <div key={multfilm.id} className={`multfilms_list ${multfilm.direction}`}>
            {authInfo.features
            && authInfo.features.map((features) => (
              features === multfilm.abbreviated && <div>fuck</div>
            ))}
            <Link to={`/multfilm?name=${multfilm.abbreviated}`}>
              <div className={`multfilms_list_block ${multfilm.direction}`}>
                <img alt={multfilm.logo} className="multfilms_list_logo" src={multfilm.logo} />
              </div>
            </Link>
            <span>
              {multfilm.name}
            </span>
          </div>
        ))))}
    </div>
  );
};

export default Progres;